import React, { Fragment } from 'react'

const Components=(props)=>{
    const {type, varients,onChangeVarient, selected}= props;

    const onClick=(e)=>{
        onChangeVarient(type,e.target.dataset.id,e.target.dataset.price);
    }
    return(
        < >
            <h3 className="componentType">{type}</h3>
            <ul>
                {varients.map((item)=>{
                    return(
                        <li key={item.id} data-id={item.id} data-price={item.price} onClick={onClick} className={`varient ${item.id==selected? 'selected' : ''}`}>{item.description}</li>
                    )
                })}
            </ul>
        </>
    )
}

export default Components