import React, {usedefaultData, useState} from 'react'
import Components from './Components'
import Description from './Description'
import Price from './Price'
import appleSrc from './images/mac.jpg'
import data from './data.json'


const App=()=>{
  const {processors, memory, graphics, storage}= data;
   const [defaultData, setdefaultData]= useState({
    price: 239900,
    processorSelected:1,
    graphicsSelected:1,
    memorySelected:1,
    storageSelected:1
  })

  const onChangeVarient=(type, id, price)=>{
    
    setdefaultData({
      price: Number(defaultData.price)+ Number(price),
      processorSelected: type==="Processor"? id: defaultData.processorSelected,
      graphicsSelected: type==="Graphics" ? id: defaultData.graphicsSelected,
      memorySelected: type==="Memory" ? id: defaultData.memorySelected,
      storageSelected: type==="Storage" ? id: defaultData.storageSelected

    })
    
  }

 
    return(
      <div className="App">
      <div className="AppContainer" data-testid="app-container">
        <div className="left">
          <img src={appleSrc}/>
        </div>
        <div className="right">
          <Description processor={processors.find((pro)=>pro.id==defaultData.processorSelected)}
                      graphics={graphics.find((gra)=>gra.id==defaultData.graphicsSelected)}
                      memory={memory.find((memo)=>memo.id==defaultData.memorySelected)}
                      storage={storage.find((sto)=>sto.id==defaultData.storageSelected)}/>
          <hr/>
          <Components type={'Processor'} varients={processors} onChangeVarient={onChangeVarient} selected={defaultData.processorSelected}/>
          <Components type={'Graphics'} varients={graphics} onChangeVarient={onChangeVarient}  selected={defaultData.graphicsSelected}/>
          <Components type={'Memory'} varients={memory} onChangeVarient={onChangeVarient} selected={defaultData.memorySelected}/>
          <Components type={'Storage'} varients={storage} onChangeVarient={onChangeVarient} selected={defaultData.storageSelected}/>

        </div>
      </div>
      <Price price={defaultData.price}/>
      </div>
    )
  
}
export default App;
